export default {
    props: {
        name: {
            type: String,
            required: true,
            default: () => 'undefined'
        },
        description: {
            type: String,
            required: true,
            default: () => 'undefined'
        },
        freezbeId: {
            type: String,
            required: true,
            default: () => 'undefined'
        },
        steps: {
            type: String,
            required: true,
            default: () => 'undefined'
        },
        id: {
            type: Number,
            required: false,
            default: () => 1
        }
    },
    methods: {
        remove() {
            this.$parent.removeProcede(this.id)
        },
        update() {
            this.$router.push('EditProcede')
        }
    }
}
